$background-color: white;
$content-backgound: #2a54ad;
$secondary-content: #f8ad2a;
$third-content: #702bd4;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
h1 {
  font-size: 2.6rem;
}
li,
button,
label,
input,
p {
  font-size: 2rem;
}
h2 {
  font-size: 4.8rem;
}
h3 {
  font-size: 3rem;
}
h4 {
  font-size: 2.8rem;
}
a {
  text-decoration: none;
  color: white;
}

#contact {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-container {
  display: flex;
  border-radius: 50px;
  background: $content-backgound;
  justify-content: center;
  align-items: center;
  color: white;
  padding: 15rem;
  min-width: 90vw;
  width: 90vw;
  margin-top: 10rem;
  margin-bottom: 10rem;
}

.form {
  margin-right: 15rem;
}

.form-div {
  background: white;
  padding: 10rem;
  color: #2a2a2a;
  border-radius: 50px;
  display: flex;
  textarea {
    width: 100%;
    height: 150px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #f8f8f8;
    resize: none;
  }
  button {
    margin-top: 3rem;
    background: $secondary-content;
  }
  .name-form {
    margin-bottom: 2rem;
    label {
      margin-bottom: 1rem;
    }
  }
  .email-form {
    margin-bottom: 1rem;
  }
}

.email {
  display: flex;
  margin-bottom: 5rem;
  align-items: center;
  width: 60%;
  border-radius: 50px;
  transition: 0.5s;
  cursor: pointer;
  .email-icon {
    margin-right: 2rem;
  }
}

.number {
  display: flex;
  align-items: center;
  margin-bottom: 3rem;
  width: 40%;
  border-radius: 50px;
  transition: 0.5s;
  cursor: pointer;
  .number-icon {
    margin-right: 2rem;
  }
}

.social-links {
  width: 15vw;
  display: flex;
  gap: 3rem;
  padding: 1rem;
}

h2 {
  margin-bottom: 1rem;
}

.info {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
  border-radius: 25px;
  width: 30vw;
}

@media screen and (max-width: 964px) {
  .form-container {
    flex-direction: column;
    background: none;
    margin-top: 0;
    padding-bottom: 0;
  }
  .form {
    margin-right: 0rem;
    width: 75vw;
    h2 {
      font-size: 2.8rem;
    }
    h4 {
      font-size: 1.7rem;
    }
    h3 {
      font-size: 2rem;
    }
  }

  .form-div {
    background: white;
    padding: 10rem;
    color: #2a2a2a;
    border-radius: 50px;
    display: flex;
    textarea {
      width: 100%;
      height: 150px;
      padding: 12px 20px;
      box-sizing: border-box;
      border: 2px solid #ccc;
      border-radius: 4px;
      background-color: #f8f8f8;
      resize: none;
    }
    button {
      margin-top: 3rem;
      background: $secondary-content;
    }
    .name-form {
      margin-bottom: 2rem;
      label {
        margin-bottom: 1rem;
      }
    }
    .email-form {
      margin-bottom: 1rem;
    }
  }

  .email {
    display: flex;
    margin-bottom: 5rem;
    align-items: center;
    width: 60vw;
    border-radius: 50px;
    transition: 0.5s;
    cursor: pointer;
    .email-icon {
      margin-right: 2rem;
    }
  }

  .number {
    display: flex;
    align-items: center;
    margin-bottom: 3rem;
    width: 40vw;
    border-radius: 50px;
    transition: 0.5s;
    cursor: pointer;
    .number-icon {
      margin-right: 2rem;
    }
  }

  .social-links {
    width: 75vw;
    display: flex;
    justify-content: center;
    gap: 3rem;
    padding: 1rem;
    margin-bottom: 5rem;
  }

  h2 {
    margin-bottom: 1rem;
  }

  .info {
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
    border-radius: 25px;
    width: 30vw;
  }
}
